.search-bar {
  margin: 20px 8px;
}

.search-bar__input {
  box-sizing: border-box;
  color: #3d4e61;
  background: #fff;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  width: 100%;
  font-family: inherit;
  font-weight: 300;
  transition: box-shadow .3s;
  box-shadow: 0 1px 8px #3d4e611a;
}

.form__big:before, .form__small:before {
  content: "";
  background-image: url("Search.ccd3c07b.svg");
  position: absolute;
}

.form__big:before {
  width: 19px;
  height: 19px;
  top: 45px;
  left: 35px;
}

.form__small:before {
  background-size: 11px;
  width: 11px;
  height: 11px;
  top: 125px;
  left: 21px;
}

.smaller-bar {
  height: 42px;
  padding-left: 33px;
  font-size: 14px;
}

.bigger-bar {
  height: 70px;
  padding-left: 62px;
  font-size: 16px;
}

.search-bar__input:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.search-bar__input::placeholder {
  text-shadow: none;
  font-weight: 300;
}

.search-bar__input:focus {
  text-shadow: 0 4px 4px #00000040;
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
  border-radius: 3px;
  outline: none;
  font-weight: 800;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
  font-weight: 400;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: 800;
}

body {
  margin: 0;
  font-family: Avenir, sans-serif;
}

